import React from "react";
import styled from "styled-components";
import Song from "../components/Song";
import axios from "axios";
import { Link } from "react-router-dom";
import { withRouter } from "react-router";

export class Artist_title extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      id: props.match.params.id
    };
  }
  async componentDidMount() {
    const id = this.state.id;
    const proxyURL = "https://cors-anywhere.herokuapp.com/";
    const URL = `https://api.deezer.com/artist/${id}/top?limit=50`;
    const {
      data: { data }
    } = await axios.get(proxyURL + URL, {
      method: "GET",
      headers: {
        "Access-Control-Allow-Origin": "*",
        "x-rapidapi-host": "deezerdevs-deezer.p.rapidapi.com",
        "x-rapidapi-key": "03afeef36cmsh1bdcf2e9a60f303p19457cjsn1f84e9465bd0"
      }
    });
    this.setState({
      data: data
    });
  }
  render() {
    return (
      <Container>
        <Songs>
          {this.state.data ? (
            this.state.data.map((each, index) => (
              <Section key={index}>
                <Info>
                  <Div>
                    <Title>{each.album.type} : </Title>
                    <Link
                      to={`/album/${each.album.id}`}
                      style={{ textDecoration: "none", color: "black" }}
                    >
                      <Content>{each.album.title}</Content>
                    </Link>
                  </Div>
                  <Div>
                    <Title>Contributors : </Title>
                    {each.contributors.map((ele, index) =>
                      index < 2 ? (
                        <Link
                          to={`/artist/${ele.id}`}
                          key={index}
                          style={{ textDecoration: "none", color: "black" }}
                        >
                          <Content>
                            {index < each.contributors.length - 1 &&index <1
                              ? `${ele.name} & `
                              : ele.name}
                          </Content>
                        </Link>
                      ) : null
                    )}
                  </Div>
                </Info>
                <Song
                  key={index}
                  id={index}
                  title={each.title}
                  preview={each.preview}
                  artist={each.artist.name}
                  album={each.album.cover_small}
                ></Song>
              </Section>
            ))
          ) : (
            <Loading>"Loading..."</Loading>
          )}
        </Songs>
      </Container>
    );
  }
}

const Container = styled.div`
  width: 100%;
  min-height: 100vh;
  background-color: rgba(150, 150, 150, 0.6);
  display: flex;
  justify-content: center;
`;
const Loading = styled.div`
  width: 80%;
  background-color: rgba(240, 240, 240, 0.85);
  height:25px;
  padding:30px;
`;
const Songs = styled.div`
  width: 80%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  padding: 50px 0;
`;
const Section = styled.div`

`;
const Div = styled.div`
/* background-color:pink;
width:100%;
  display: flex;
  flex-direction:column;
  padding:10px;
  justify-content:center; */
  `;
const Info = styled.div`
  background-color: rgba(240, 240, 240, 0.85);
  display: grid;
  grid-template-columns: .7fr 1.3fr;
  justify-items:flex-start;
  align-content:center;
  padding:10px 20px;
  height:70px;
  border-bottom:1px solid darkgray;
`;
const Title = styled.div`
  font-size: 1rem;
  font-weight:border;
`;
const Content = styled.div`
  font-size:1rem;

`;
export default Artist_title;
